<template>
  <view-template id="login_site">
    <div id="logo">
      Saritagun
    </div>
    <div class="login">
      <form
        method="post"
        @submit.prevent="handleLogin"
      >
        <h1>LOGOWANIE</h1>
        <input
          v-model="user.username"
          type="text"
          placeholder="Nazwa uytkownika"
          required
        >
        <input
          v-model="user.password"
          type="password"
          placeholder="Haso"
          required
        >
        <p
          v-if="error"
          class="message message--error"
        >
          {{ error }}
        </p>
        <button type="submit">
          Zaloguj si
        </button>
      </form>
      <a
        role="button"
        @click="fbLogin('test')"
      >
        <i class="fab fa-facebook" />
        Zaloguj si przez facebook
      </a>
      <facebook-login
        class="button"
        app-id="1369553153510750"
        @login="getUserData"
        @logout="onLogout"
        @get-initial-status="getUserData"
      />
    </div>
  </view-template>
</template>

<script>

import api from '../api'
import ViewTemplate from '../components/ViewTemplate'
import facebookLogin from 'facebook-login-vuejs'

export default {
  // vuetify: new Vuetify(),
  name: 'Homepage',
  components: {
    ViewTemplate,
    facebookLogin
  },
  data () {
    return {
      error: null,
      user: {
        username: '',
        password: ''
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    this.$children[0].beforeRouteLeave(to, from).then(() => {
      next()
    })
    next(false)
  },
  methods: {
    async fbLogin (test) {
      alert(test)
      const response = await api.loginFacebook()
      console.log(response)
    },
    async handleLogin () {
      const response = await api.login(this.user)

      if (response.status !== 200) {
        return (this.error = response.data.message)
      }
      this.$store.dispatch('login', response.data)
      this.$router.push({ name: 'homepage' })
    },
    getUserData (obj) {
      console.log(obj)
    },
    onLogout (obj) {
      console.log(obj)
    }
  }
}
</script>
